import styled from 'styled-components'
import Img from 'gatsby-image'
import media from 'styled-media-query'

export const ProfissionalAvatarWrapper = styled(Img)`

    display: inline-block;
    width: 205px;
    height: 192px;

    ${media.lessThan('huge')` 
        width: 182px;
        height: 172px;
    `}

    
    ${media.lessThan('large')` 
        width: 91px;
        height: 86px;
    `}

`