import React from "react"
import { StaticQuery, graphql } from 'gatsby'

import * as S from './styled'

const VocePodeAvatar = props => (
    <StaticQuery
      query={graphql`
        query {
          images: allFile {
            edges {
              node {
                relativePath
                name
                childImageSharp {
                  fluid(maxWidth: 55) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      `}
      render={data => {
        const image = data.images.edges.find(n => {
          return n.node.relativePath.includes(props.filename);
        });
        if (!image) {
          return null;
        }
        return <S.VocePodeAvatarWrapper alt={props.alt} fluid={image.node.childImageSharp.fluid} />;
      }}
    />
  );

export default VocePodeAvatar