
import React from 'react'

import { useStaticQuery, graphql } from 'gatsby'

import * as S from './styled'

const HeaderImgTwo = () => {
    const { imageTwo } = useStaticQuery(
        graphql`
            query {
                imageTwo: file(relativePath: { eq: "header_02.jpg"}) {
                    childImageSharp {
                        fluid(maxWidth: 249) {
                            ...GatsbyImageSharpFluid_tracedSVG
                        }
                    }
                }
            }
        `
    )

    return <S.HeaderImgTwoWrapper fluid={imageTwo.childImageSharp.fluid} />
}

export default HeaderImgTwo