import React from 'react'

import * as S from './styled'
import ProfissionalAvatar from '../ProfissionalAvatar'

const ProfissionalItem = ({filename, title, subtitle, description}) => (
    <S.ProfissionalItemWrapper>
       <ProfissionalAvatar filename={filename} />
       <S.ProfissionalBoxText>
            <S.ProfissionalTitle>{title}</S.ProfissionalTitle>
            <S.ProfissionalSubtitle>{subtitle}</S.ProfissionalSubtitle>
            <S.ProfissionalDescription>
                {description}
            </S.ProfissionalDescription>
       </S.ProfissionalBoxText> 
    </S.ProfissionalItemWrapper>
)
export default ProfissionalItem