import React from 'react'

import { Link } from 'gatsby-plugin-modal-routing'

import IdeiaItem from '../IdeiaItem'

import * as S from './styled'

const Ideias = () => (
    <S.IdeiasWrapper>
        <S.IdeiasTitle>Confira ideias e conecte com a que melhor lhe representa</S.IdeiasTitle>
        <S.IdeiasItemBox>
            <Link to="/busco-uma-ideia/" asModal>
                <IdeiaItem filename="profissional_01.jpg" itemTitle="Busco uma ideia"/>
            </Link>
            <Link to="/tenho-uma-ideia/" asModal>
                <IdeiaItem filename="profissional_02.jpg" itemTitle="Tenho uma ideia"/>
            </Link>
        </S.IdeiasItemBox>
    </S.IdeiasWrapper>
)
export default Ideias