import React from 'react'

import { useStaticQuery, graphql } from 'gatsby'

import * as S from './styled'

const HeaderImgOne = () => {
    const { imageOne } = useStaticQuery(
        graphql`
            query {
                imageOne: file(relativePath: { eq: "header_01.jpg"}) {
                    childImageSharp {
                        fluid(maxWidth: 256) {
                            ...GatsbyImageSharpFluid_tracedSVG
                        }
                    }
                }
            }
        `
    )

    return <S.HeaderImgOneWrapper fluid={imageOne.childImageSharp.fluid} />
}

export default HeaderImgOne