import React, { useState } from "react"

import addToMailChimp from 'gatsby-plugin-mailchimp'

import * as S from './styled'

import { toast, ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css'; 

const Email = () => {
    const [email, setEmail] = useState('')
  
    const handleSubmit = async (e) => {
      e.preventDefault();
      const result = await addToMailChimp(email)
      validation(result)
      setEmail('')  
    }
  
    const handleChange = e => {
      setEmail(e.target.value)
    }
    const validation = (result) => {
        if (result !== null && result.result === 'success') {
            toast.success(`${email} inscrito com sucesso!`);
          } else if (result !== null && result.result === 'error' && result.msg.includes(email) && result.msg.includes('subscribed')) {
            toast.info(`${email} j est inscrito!`)
          } else {
            toast.error(`Ops ocorreu um erro ao se inscrever! Tente novamente mais tarde.`)
          }
    }
    return (
        <S.EmailWrapper>
            <ToastContainer />
            <form onSubmit={handleSubmit}>
                <S.EmailInput value={email} onChange={handleChange} />
                <S.EmailButton>Tenho Interesse</S.EmailButton>
            </form>
        </S.EmailWrapper>
    )
  }

export default Email